.footer {
    color: #fff;
    background-color: #303740;
}

.footer-top {
    padding: 20px 0px;
    border-bottom: 1px solid #444;
}

.footer .inner {
    display: flex;
    max-width: 1440px;
    height: 100%;
}

.footer-gnb {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.footer-gnb__item {
    padding: 16px 0;
    margin-right: 20px;
}

.footer-gnb__item:last-child {
    margin-right: 0;
}

.footer-gnb__title {
    padding-bottom: 16px;
    font-weight: 700;
    font-size: 14px;
    color: #eee
}

.footer-gnb__sublist {}

.footer-gnb__sublist-item {
    margin-bottom: 7px;
}

.footer-gnb__sublist-link {
    display: block;
    font-weight: 400;
    color: #bdbdbd;
}

.footer-gnb__sublist-link:hover {
    text-decoration: underline;
}

.get-the-app {
    text-align: center;
    line-height: normal;
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    color: #ced4da;
}

.footer-bottom {
    padding: 20px 0
}

.footer-bottom .inner {
    justify-content: space-between;
}

.footer-bottom__left {
    line-height: 1.5;
    font-size: 12px;
    color: #bdbdbd;
}

.footer-bottom__logo-and-terms {
    display: flex;
    align-items: center;
    margin-bottom: 8px;
}

.footer-bottom__logo-and-terms li {
    position: relative;
}


.footer-bottom__logo-and-terms li:not(:first-child)::before {
    content: "|";
    margin: 0 8px;
    font-size: 12px;
    color: #bdbdbd;
}

.footer-bottom__logo-and-terms .logo {
    width: 85px;
    padding-bottom: 5px;
}

.footer-bottom__logo-and-terms .logo img {
    max-width: 100%;
}

.footer-bottom__logo-and-terms li a {
    display: inline-block;
    line-height: 1.5;
    font-weight: 700;
    font-size: 12px;
    color: #bdbdbd;
}

.footer-bottom__address {
    margin-bottom: 8px;
    font-style: normal;
}

.footer-bottom__copyright {}

.footer-bottom__right {}

.footer-bottom__sns-links {
    display: flex;
    align-items: center;
    gap: 0 10px;
    height: 100%;
}

.footer-bottom__sns-links li {}

.footer-bottom__sns-links li a {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 34px;
    height: 34px;
    border-radius: 100%;
    background: #bdbdbd;
    text-align: center;
    line-height: 35px;
}

.footer-bottom__sns-links li a i {
    display: flex;
    justify-content: center;
    align-items: center;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    color: #333;
}