/* 메인 섹션 공통 */
.main-section {
    padding: 24px 0;
}

.main-section__title {
    margin-bottom: 4px;
}

.main-section__title a {
    display: inline-flex;
    align-items: center;
    font-weight: 700;
    font-size: 22px;
    color: #1e1e1e;
    line-height: 1.5;
}

.main-section__title .red {
    padding-top: 8px;
    margin-left: 4px;
    font-weight: 700;
    font-size: 12px;
    color: #fa4c3d;
}

.main-section__title .icon {
    margin-left: 8px;
}

.main-section__desc {
    margin-bottom: 16px;
}

.main-section__desc p {
    font-size: 14px;
    color: #757575;
    line-height: 1.5;
}

/* 상단 배너 슬라이드 */
.main-section.top-banner-section {
    padding-top: 0;
}

.top-banner-slide__item .inner {
    max-width: 1200px;
}

.top-banner-slide__link {
    display: flex;
    align-items: center;
    height: 320px;
    padding-left: 32px;
}

.top-banner-slide__left {
    width: 45%;
    translate: 0 -10%;
}

.top-banner-slide__tag {
    display: inline-flex;
    align-items: center;
    height: 24px;
    padding: 0 10px;
    margin-bottom: 16px;
    font-weight: 700;
    font-size: 15px;
    border-radius: 4px;
}

.top-banner-slide__title {
    margin-bottom: 8px;
    white-space: pre-line;
    font-weight: 700;
    font-size: 30px;
    line-height: 1.5;
}

.top-banner-slide__text {
    white-space: pre-line;
    font-size: 16px;
    line-height: 1.6;
}

.top-banner-slide__right {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 55%;
}

.top-banner-slide__img-wrap {
    height: 320px;
}

.top-banner-slide__img-wrap img {
    height: 100%;
}

.top-banner-controller {
    border-bottom: 1px solid #e9ecef;
}

.top-banner-controller .inner {
    display: flex;
    align-items: center;
    max-width: 1200px;
    height: 64px;
    padding: 0 32px;
}

.top-banner-controller__left {
    position: relative;
    padding-right: 16px;
    margin-right: 16px;
}

.top-banner-controller__left::after {
    content: '';
    position: absolute;
    top: 50%;
    right: -1px;
    width: 1px;
    height: 36px;
    background-color: #dee2e6;
    transform: translateY(-50%);
}

.top-banner-controller__page-controller {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 135px;
    height: 36px;
    padding: 0 16px;
    color: #fff;
    font-size: 14px;
    line-height: 1;
    border-radius: 20px;
    background-color: rgba(0, 0, 0, 0.5);
}

.top-banner-controller__controller {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 50px;
}

.top-banner-controller__controller button {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 0;
    background-color: transparent;
}

.top-banner-controller__controller button span {
    font-size: 20px;
}

.top-banner-controller__right {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.top-banner-controller__right .swiper-pagination {
    display: flex;
    align-items: center;
    gap: 0 8px;
    top: 0 !important;
    left: 0 !important;
    height: 100%;
}

.top-banner-controller__tag {
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 0 0 auto;
    height: 36px;
    padding: 0 16px;
    font-size: 14px;
    font-weight: 700;
    color: #495057;
    border-radius: 20px;
    background-color: #fff;
    box-shadow: inset 0 0 0 1px #ced4da;
    cursor: pointer;
}

.top-banner-controller__tag.active {
    color: #00c471;
    box-shadow: inset 0 0 0 2px #00c471;
}

/* 메인 검색창 */
/* .main-search{} */

/* 메인강의리스트 */
.lecture-list {
    position: relative;
    max-height: 295px;
    padding: 6px 0;
}

.lecture-list__item {
    width: 20%;
    cursor: pointer;
}

.card-link {
    display: block;
    position: relative;
}

.card-link:hover .card-back {
    display: block;
}

.card-front {
    display: flex;
    flex-direction: column;
}

.card-img figure {
    position: relative;
    width: 100%;
    padding-top: 65.1%;
    /* 16:10 비율 유지 */
}

.card-img img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.card-title {
    height: 3em;
    font-weight: 700;
    font-size: 16px;
    color: #454545;
    line-height: 1.5;
}

.card-writer {
    font-size: 14px;
    color: #7d7d7d;
}

.card-rating {
    display: flex;
    align-items: end;
}

.rating-star {
    /* 별 색상 추가 */
}

.rating-review-cnt {
    margin-bottom: 3px;
    font-size: 8px;
    color: #4a4a4a;
}

.card-price {
    margin-bottom: 8px;
    font-weight: 700;
    font-size: 16px;
    color: #175cbe;
}

.card-price .line-through {
    margin-right: 4px;
    font-size: 14px;
    color: #595959;
    opacity: 0.7;
}

.card-tags ul {
    display: flex;
    gap: 8px;
}

.card-tags li {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2px 8px;
    font-size: 12px;
    color: #454545;
    line-height: 1.5;
    background-color: hsl(321, 63%, 90%);
}

.card-back {
    display: none;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    padding: 12px 12px 100px;
    /* opacity: 0.8; */
    background-color: rgba(0, 0, 0, 0.8);
}

.card-link:hover .card-back {
    display: block;
}

.card-back__title {
    margin-bottom: 14px;
    font-weight: 700;
    font-size: 15px;
    color: #fff;
    line-height: 1.3;
}

.card-back__infomation {
    font-size: 12px;
    color: lightseagreen;
}

.card-back__button-wrap {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: end;
    position: absolute;
    bottom: 12px;
    right: 12px;
}

.card-back__button-item {
    position: relative;
}

.card-back__button-label {
    display: none;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0;
    left: -126px;
    width: 120px;
    padding: 7px 8px;
    font-weight: 500;
    font-size: 11px;
    color: #000;
    border-radius: 4px;
    background-color: #ffdd57;
    z-index: 1;
}

.card-back__button-label::after {
    content: '';
    position: absolute;
    top: 50%;
    right: -2px;
    width: 8px;
    height: 8px;
    background-color: #ffdd57;
    transform: rotate(45deg) translateY(-50%);
    z-index: 0;
}

.card-back__button-item:hover .card-back__button-label {
    display: flex;
}

.card-back__button {
    padding: 4px;
    background-color: transparent;
    border: 0;
}

.card-back__button .icon {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    color: #fff;
}

.card-back__button-item.active .icon {
    color: yellow;
}

.card-back__button-item:hover .icon {
    color: yellow;
}

.lecture-list__navigation button {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 50%;
    width: 44px;
    height: 44px;
    padding: 10px;
    opacity: 0.85;
    color: #333;
    border: 1px solid #ccc;
    border-radius: 50%;
    background-color: #fff;
    translate: 0 -50%;
    z-index: 1;
}

.lecture-list__navigation button.swiper-button-disabled {
    color: #efefef;
    border: 1px solid #efefef;
    box-shadow: 0 2px 8px 0 transparent;
    cursor: not-allowed;
}

.lecture-list__navigation-left {
    left: -22px;
}

.lecture-list__navigation-right {
    right: -22px;
}

/* 메인로드맵리스트 */
.roadmap-list {
    position: relative;
}

.roadmap-list__link {
    display: block;
    padding-bottom: 12px;
}

.roadmap-list__thumbnail {
    padding-bottom: 8px;
}

.roadmap-list__thumbnail-img {
    width: 100%;
}

.roadmap-list__title {
    font-weight: 700;
    font-size: 15px;
    color: #363636;
}

/* 메인 읽어보기 리스트 */
.read-list {
    margin-top: 16px;
}

.read-list__list {
    display: flex;
    gap: 0 20px;
}

.read-list__item {
    width: 33.3333333333%;
}

.read-list__link {
    display: block;
}

.read-list__thumbnail {
    margin-bottom: 12px;
}

.read-list__thumbnail-wrap {
    position: relative;
    padding-bottom: 52.3333333333%;
}

.read-list__thumbnail-img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.read-list__title {
    padding-bottom: 4px;
    font-weight: 700;
    font-size: 18px;
    color: #333;
    line-height: 1.5;
}

.read-list__desc {
    font-size: 16px;
    color: #595959;
}

/* 메인 수강평 더보기 */
.main-review-section .inner {
    display: flex;
}

.main-review__left {
    width: 100%;
    max-width: 480px;
    padding: 40px 16px 40px 0;
}

.main-review__right {
    width: 100%;
}

.main-review__title {
    margin-bottom: 20px;
    font-weight: 700;
    font-size: 36px;
    line-height: 1.5;
}

.main-review__title .color {
    color: #47c880;
}

.main-review__desc {
    margin-bottom: 36px;
    font-size: 16px;
    color: #757575;
    line-height: 1.5;
}

.main-review__button-wrap {
    display: flex;
    gap: 0 8px;
}

.main-review__button-link {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 8px 16px;
    font-size: 14px;
    color: #333;
    border: 1px solid #1dc078;
    transition: background-color 0.2s;
}

.main-review__button-link:hover {
    background-color: #1dc0781f;
}

.main-review__right {
    position: relative;
}

.main-review__right::after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 48px;
    background: linear-gradient(180deg, hsla(0, 0%, 100%, 0), hsla(0, 0%, 100%, 0.72));
    z-index: 1;
}

.main-review__slide {
    height: 480px;
}

.main-review__slide-item {
    height: auto !important;
    padding: 16px;
    background-color: #f9f9f9;
    border: 1px solid #e5e5e5;
    border-radius: 4px;
    box-shadow: 2px 3px rgba(0, 10, 18, 0.1), 0 0 0 1px rgba(0, 10, 18, 0.1);
    cursor: grab;
}

.main-review__slide .swiper-wrapper {
    position: relative;
    top: 30px;
}

.main-review__slide-item-top {
    display: flex;
    gap: 0 8px;
    padding-bottom: 16px;
}

.main-review__slide-item-user {
    font-size: 15px;
    color: #757570;
}

.main-review__slide-item-time {
    font-size: 14px;
    color: #979797;
}

.main-review__slide-item-title {
    display: flex;
    align-items: center;
    gap: 0 4px;
    font-weight: 700;
    font-size: 18px;
    color: #595950;
    line-height: 1.5;
    margin-bottom: 4px;
}

.main-review__slide-item-title:hover {
    text-decoration: underline;
}

.main-review__slide-item-review {
    font-size: 15px;
    color: #333;
    line-height: 1.5;
}

/* 메인 다양한 서비스 */
.main-service-section {
    padding: 48px 0;
    background-color: #f9f9f9;
}

.apply-wrap {
    display: flex;
    gap: 0 10px;
}

.apply-item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 370px;
    height: 330px;
    padding: 48px;
    border-radius: 4px;
    background-color: #fff;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
}

.apply-item__top {
}

.apply-item__bottom {
}

.apply-item__title {
    margin-bottom: 8px;
    line-height: 1;
    font-size: 17px;
    color: #363636;
}

.apply-item__desc {
    white-space: pre-line;
    /* word-break: keep-all; */
    line-height: 1.5;
    font-size: 15px;
    color: #757575;
}

.apply-item__link {
    display: inline-block;
    padding: 12px 16px;
    font-weight: 700;
    font-size: 15px;
    color: #fff;
    border-radius: 20px;
    background-color: #1dc078;
}
