/* Topbar */
.topbar {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 40px;
  background-color: #fff;
  border-bottom: 1px solid #f1f3f5;
  z-index: 30;
}

.topbar .inner {
  max-width: 1440px;
}

.topbar__content {
  display: flex;
  justify-content: space-between;
}

.topbar__list {
  display: flex;
}

.topbar__list li {
  display: flex;
  justify-content: center;
  align-items: center;
}

.topbar__list li:not(:last-child)::after {
  content: '';
  display: block;
  margin: 0 8px;
  width: 1px;
  height: 12px;
  background: #d5dbe2;
}

.topbar__list a {
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 500;
  color: #858a8d;
}

.topbar__list li.active a {
  font-weight: 700;
  color: #1b1c1d;
}

.topbar__service .topbar__list a {
  font-weight: 700;
}

.topbar__links .topbar__list a {
  font-size: 13px;
  color: #3e4042;
}

.topbar__list svg {
  margin-right: 4px;
}

/* Inquiry Button */
.inquiry-btn {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;
  position: fixed;
  right: 18px;
  bottom: 50px;
  width: 157px;
  height: 74px;
  z-index: 99;
}

.inquiry-btn__link {
  display: block;
  width: 100%;
  height: 100%;
}

.inquiry-btn__img {
  width: 100%;
  height: 100%;
}
