.login-modal {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 200;
}
.login-modal__background {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0.4;
  background-color: #000;
  z-index: 199;
}
.login-modal__modal {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: absolute;
  width: 100%;
  max-width: 360px;
  padding: 24px;
  background-color: #fff;
  border-radius: 8px;
  z-index: 201;
}
.login-modal__close {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 30px;
  height: 30px;
  background-color: transparent;
  border: 0;
}
.login-modal__logo {
  margin-top: 20px;
  margin-bottom: 12px;
  text-align: center;
}
.login-modal__form {
  display: flex;
  flex-direction: column;
  gap: 12px 0px;
  width: 100%;
  margin-bottom: 12px;
}
.login-modal__input {
  padding: 12px;
  border: 1px solid lightgray;
  border-radius: 4px;
  outline: 0;
}
.login-modal__input:focus {
  border-color: #00c471;
}
.login-modal__submit {
  padding: 12px;
  margin-top: 12px;
  color: #fff;
  border: 0;
  border-radius: 4px;
  background-color: #00c471;
}
.login-modal__more-action {
  display: flex;
  gap: 0 16px;
  margin-bottom: 24px;
}
.login-modal__more-action-item {
  position: relative;
  font-size: 11px;
  color: #616568;
}
.login-modal__more-action-item::before {
  content: '';
  position: absolute;
  bottom: -1px;
  width: 100%;
  height: 1px;
  background-color: grey;
}
.login-modal__more-action-item:not(:last-child)::after {
  content: '';
  position: absolute;
  top: 50%;
  right: -8px;
  width: 1px;
  height: 10px;
  background-color: #858a8d;
  translate: 0 -50%;
  cursor: default;
}

.login-modal__social-login {
  width: 100%;
}
.login-modal__social-login-title {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0 8px;
  width: 100%;
  font-size: 11px;
  margin-bottom: 12px;
}
.login-modal__social-login-title .title {
  flex-shrink: 0;
  color: #abb0ab;
}
.login-modal__social-login-title .line {
  display: block;
  width: 100%;
  height: 1px;
  background-color: #f1f3f5;
}
.login-modal__social-login-list {
  display: flex;
  justify-content: center;
  gap: 0 12px;
}
.login-modal__social-login-item {
  position: relative;
}
.login-modal__social-login-button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 44px;
  height: 44px;
  background-color: transparent;
  border: 0;
  border-radius: 4px;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.2);
}
.kakao {
  background-color: #fae500;
}
.google {
  background-color: #f8f8f8;
}
.github {
  background-color: #3c4043;
}

.login-modal__tooltip {
  position: absolute;
  top: -34px;
  left: 50%;
  width: 80px;
  padding: 8px;
  font-weight: 700;
  font-size: 12px;
  color: #fff;
  line-height: 1;
  text-align: center;
  background-color: #495057;
  border-radius: 8px;
  translate: -50% 0;
  z-index: 1;
}
.login-modal__tooltip::before {
  content: '';
  position: absolute;
  left: 50%;
  bottom: -4px;
  width: 8px;
  height: 8px;
  background-color: #495057;
  transform: translateX(-50%) rotate(45deg);

}
