/* 회원가입 */
.signup {
  padding: 72px 0;
}

.signup .inner {
  max-width: 360px;
  padding: 0 16px;
}

.signup-header {
  margin-bottom: 24px;
}

.signup-title {
  font-size: 24px;
  color: #212529;
  text-align: center;
  line-height: 1.5;
  margin-bottom: 8px;
}

.signup-text-slide p {
  text-align: center;
  letter-spacing: -0.3px;
  font-size: 14px;
}

.signup-form__row:not(:last-child) {
  margin-bottom: 16px;
}

.signup-form__row:last-child {
  padding-top: 16px;
}

.signup-form__label {
  display: block;
  margin-bottom: 8px;
  font-weight: 500;
  font-size: 16px;
  color: #212529;
}

.signup-form__input-wrap {
  margin-bottom: 4px;
}

.signup-form__input-wrap.has-button {
  position: relative;
}

.signup-form__input-wrap.has-button .signup-form__input {
  padding-right: 40px;
}

.signup-form__input {
  display: block;
  width: 100%;
  padding: 15px;
  font-size: 15px;
  border-radius: 8px;
  border: 1px solid #aaa;
  outline: none;
  box-sizing: border-box;
}
.signup-form__input.error {
  color: #fa5252;
  border-color: #fa5252;
}
.signup-form__input.error::placeholder {
  color: #fa5252;
}
.signup-form__input.error:focus {
  box-shadow: rgb(250, 82, 82) 0px 0px 0px 1px;
}

.signup-form__error-text {
  display: flex;
  align-items: center;
  gap: 0 8px;
  font-size: 14px;
  color: #868296;
}

.signup-form__error-text .icon {
  font-size: 16px;
}

.signup-form__error-text.success {
  color: #00c471;
}

.signup-form__error-text.error {
  color: #fa5252;
}

.signup-form__visible-button {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 50%;
  right: 4px;
  width: 32px;
  height: 32px;
  color: gray;
  border: 0;
  border-radius: 8px;
  background-color: transparent;
  translate: 0 -50%;
  transition: background-color 0.2s;
}

.signup-form__visible-button:hover {
  background-color: #f2f2f2;
}

.signup-form__submit {
  width: 100%;
  padding: 12px 0;
  font-weight: 700;
  font-size: 16px;
  color: #fff;
  border: 0;
  border-radius: 8px;
  background-color: #00c471;
  transition: background-color 0.2s;
}

.signup-form__submit:hover {
  background-color: #00a760;
}

.signup-agree {
  margin: 8px 0 32px;
  font-size: 12px;
  line-height: 1.4;
}

.signup-agree__checkbox {
  display: flex;
  justify-content: center;
  gap: 0 8px;
  margin-top: 8px;
}

.signup-agree__checkbox input[type='checkbox']:checked {
  background-color: rgb(0, 196, 113);
  border-color: rgb(0, 196, 113);
}

.line-text {
  position: relative;
  display: flex;
  align-items: center;
  gap: 0 10px;
  margin-bottom: 25px;
}

.line-text::before {
  content: '';
  display: block;
  width: 100%;
  height: 1px;
  background-color: #e9ecef;
}

.line-text::after {
  content: '';
  display: block;
  width: 100%;
  height: 1px;
  background-color: #e9ecef;
}

.line-text span {
  flex-shrink: 0;
  width: 80px;
  font-size: 12px;
  color: #adb5bd;
}

.signup-social-login__list {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  width: 100%;
}

